.container {
	width: 100%;
	min-height: 48px;
	border: 1px solid #D9D9D9;
	padding: 15px;

	.list {
		min-height: 100%;
		display: flex;
		align-items: center;

		.name {
			max-height: 110px;
			width: 150px;
			overflow: hidden;
			text-align: center;
			word-wrap: break-word;
			cursor: pointer;
			transition: color 0.2s ease-in-out 0s;

			&:hover {
				color: #7a7a7a;
				text-decoration: underline;
			}
		}

		.cardsCount {
			width: 50px;
			overflow: hidden;
			margin-left: 83px;
			text-align: center;
		}

		.updated {
			width: 210px;
			overflow: hidden;
			margin-left: 81px;
			text-align: center;
		}

		.userName {
			max-height: 110px;
			width: 200px;
			overflow: hidden;
			margin-left: 17px;
			text-align: center;
		}

		.actions {
			width: 100px;
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin-left: 56px;

			.teacher {

				&:disabled {
					opacity: 0.5;
				}
			}
		}

		.secondaryActions {
			width: 100px;
			display: flex;
			justify-content: center;
			align-items: center;
			margin-left: 56px;

			.teacher {

				&:disabled {
					opacity: 0.5;
				}
			}
		}
	}
}